.aboutShammah {
  padding: 0;
  position: relative;


  .bg-area {
    width: 95%;
    max-width: 1440px;
    padding: 0 10px;
    margin: 0 auto;
    height: 540px;

    &::before {
      display: block;
      content: '';
      background-color: #000;
      width: calc((100vw - 1440px) / 2 + 100% + 10px);
      min-width: 100%;
      height: 100%;

    }
  }

  .content-box {
    position: absolute;
    top: 60px;
    width: 95%;
    max-width: 1440px;
    right: 0;
    left: 0;
    margin: auto;

    header {
      width: calc(100% / 6 * 5 - 20px);
      height: 480px;
      margin: 0;
      direction: rtl;
      position: relative;

      &::before {
        border-radius: 0 10px 0 0;
        display: block;
        content: '';
        background-color: #999;
        width: calc((100vw - 1440px) / 2 + 100% + 10px);
        min-width: 100%;
        height: 100%;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
      }

      .hdr-box {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        padding-top: 120px;
        padding-left: 10px;

        h3 {}

        p {
          width: 60%;
          color: #333;
          font-size: 18px;
          line-height: 24px;
        }
      }
    }

    section {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 10px;
      width: calc(50% - 20px);
      height: 360px;
      margin: auto;

      .image {
        border-radius: 10px;
        background-color: #fff;
        width: 100%;
        height: 100%;
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
      }
    }
  }
}

.majorPortfolio {
  section {
    margin-top: 0;
    padding: 0 10px;

    .images {
      box-shadow: 0 0 30px rgba(0, 0, 0, 0.7);
      border-radius: 10px;
      overflow: hidden;

      .imgwrap {
        width: calc(100% / 1);
        height: 540px;
        background-color: #ccc;

        .imgitem {
          width: 100%;
          height: 100%;
          background-size: cover;
          background-position: center;

          * {
            visibility: hidden;
          }
        }

        //background-image: url(../assets/imgs/portfolio-01.jpg);
      }
    }

    .slick-arrow {
      color: #fff;
      width: 80px;
      height: 80px;
      z-index: 1;
      border-radius: 10px;

      &.slick-prev {
        left: 0px;
      }

      &.slick-next {
        right: 0px;
      }
    }
  }

  header {
    position: relative;
    margin-top: -40px;
    text-align: right;
    padding: 0 40px;
    z-index: 2;
  }
}

.shammahPortfolio {
  section {
    article {
      background-color: #ccc;
    }

    display: flex;
    justify-content: space-around;

    .artheader {
      position: relative;
      width: calc(25% - 20px);
      width: 25%;
      padding: 0 10px;
      direction: rtl;

      .art-box {
        height: 500px;
        width: calc((100vw - 1440px) / 2 + 100%);
        min-width: calc(100% - 20px);
        margin-right: 10px;
        background-color: #ccc;
      }

      article {
        position: absolute;
        top: 0;
        width: calc(100% - 20px);
        height: 500px;
        border-radius: 10px;

        .tabMenu {
          padding: 40px 0;
          direction: rtl;

          li {
            width: 70%;
            background-color: #333;
            line-height: 60px;
            margin-top: 10px;
            text-align: right;
            padding: 0 20px;
            font-size: 18px;
            font-weight: 700;
            color: #666;
            cursor: pointer;

            &.active {
              width: 90%;
              color: #fff;
              cursor: default
            }

            &:first-of-type {
              margin: 0;
            }
          }
        }
      }
    }

    .artcontents {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      width: calc(75% - 20px);
      width: 75%;

      article {
        position: relative;
        
        

        background-position: center;
        background-size: cover;
        border-radius: 10px;

        &.appdesign {
          width: calc(33.33% - 20px);
          height: 240px;
          box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);

          &:hover {
            box-shadow: 0 0 15px rgba(0, 0, 0, 0.4);
          }
        }

        &.uxuilesson {
          width: calc(33.33% - 20px);
          padding: 40px 0px;

          h3 {
            font-weight: 700;
            font-size: 20px;
            padding-left: 20px;
          }
          h4{font-weight: 700;}

          ul {
            margin-top: 10px;
            padding: 0 20px;
            height: 390px;
            overflow-y: auto;
            li {
              padding: 5px 0;
              p{
                font-size: 14px;
              }
            }
          }
        }

        &.appdev{
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
        }
        &.prtdesign{
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
        }
        &:nth-of-type(3)~* {
          margin-top: 20px;
        }

        .popupBtn {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }

        .popup {
          position: fixed;
          z-index: 100;
          top: 0;
          left: 0;
          width: 100vw;
          height: 100vh;
          background-color: rgba(0, 0, 0, 0.7);
          display: flex;
          justify-content: center;
          align-items: center;

          .popup-inner {
            max-width: 1200px;
            width: 80%;
            height: 640px;
            border-radius: 20px;
            position: relative;

            button {
              font-size: 24px;
              position: absolute;
              top: 10px;
              right: 10px;
            }
          }
        }
      }
    }
  }
}